.nav {
    grid-area: nav;
    background-color: green;
    padding: 20px;
}

.nav li {
    list-style-type: none;
}

.nav ul {
    margin-left: 0;
    margin-right: 0;
}

.item a {
    color: white;
    text-decoration: none;
}
.item a.active{
    color: gold;
}
.item a:hover {
    color: gold;
}
